#@Author: bjy @Date: 2019/6/5 13:46 #
<template>
  <div name="List" class="list-panel">
    <el-table ref="multipleTable" :data="userList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="35"/>
      <el-table-column prop="account" label="" width="110"/>
      <el-table-column prop="nickname" label="" width="100"/>
      <el-table-column prop="gender" label="" width="50"/>
      <el-table-column prop="type" label="" width="90"/>
      <el-table-column prop="status" label="" width="50"/>
      <el-table-column prop="date" label="" width="110"/>
      <el-table-column prop="work" label="" width="240"/>
      <el-table-column prop="description" label=""/>
      <el-table-column label="" width="130">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="viewUser(scope.row)"></el-button>
          <el-button type="text" size="mini" @click="updateUser(scope.row)"></el-button>
          <el-button type="text" size="mini" @click="deleteUser(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" :total="1000"/>
    <!--<user-dialog v-model="isUserDialogShow" :type="dialogType" :userData="userData"/>-->
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex'
  import ActivityDialog from '../ActivityDialog/ActivityDialog.vue'

  export default {
    name: 'List',
    components: {
//      UserDialog
    },
    data() {
      return {
        isUserDialogShow: false,
        dialogType: 0,
        userData: {},
        selectedList: []
      }
    },
    computed: {
      ...mapState([
        'activityList'
      ])
    },
    mounted() {
      this.getActivityList()
    },
    methods: {
      ...mapActions([
        'getActivityList'
      ]),
      // 
      handleSelectionChange(val) {
        this.selectedList = val
      },
      // 
      viewUser(data) {
        console.log('data =viewUser= ', data)

        this.dialogType = 0
        this.isUserDialogShow = true
        this.userData = data
      },
      // 
      updateUser(data) {
        console.log('data =updateUser= ', data)

        this.dialogType = 2
        this.isUserDialogShow = true
        this.userData = data
      },
      // 
      deleteUser(data) {
        console.log('data =deleteUser= ', data)

        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message.success('')
        }).catch(() => {
          this.$message.info('')
        })
      }
    }
  }
</script>

<style scoped>
  .el-pagination {
    margin: 10px 0;
    float: right;
  }
</style>




