#@Author: bjy @Date: 2019/6/5 14:48 #
<template>
  <el-dialog
    :value="value"
    name="ActivityDialog"
    :visible="isShow"
    :before-close="handleClose"
    :title="title"
    width="640px"
    center
  >
  </el-dialog>
</template>

<script>
    //  ,,:import xxx from ""
    export default{
        // name,
        name: 'activityDialog',
        prop:{
          userData: { // 
            type: Object,
            default() {
              return {}
            }
          },
          type: { // : 0-(),1-,2-
            type: Number,
            default: 0
          },
          value: {
            type: Boolean,
            default: false
          }
        },
        //  
        data () {
            return {}
        },
        // ()
        created(){
        },
        // 
        methods: {},
        // 
        computed: {},
        // 
        watch: {
          value(val) {
            this.isShow = val
          },
          isShow(val) {
            this.$emit('input', val)
          },
        },
        // 
        components: {}
    }
</script>

