<template>
      <el-tabs type="border-card">
        <div>
          <buttons :changeData="tableData"/>
        </div>
        <div>
          <search />
        </div>
        <div>
          <list @tableDataChange="changeData"/>
        </div>
      </el-tabs>
</template>

<script>
  import List from '../List/List.vue'
  import Buttons from '../Buttons/Buttons.vue'
  import Search from '../Search/Search.vue'
    //  ,,:import xxx from ""
    export default{
        // name,
        name: 'manage',
        props:['currentNode'],
        //  
        data () {
            return {
              tableData:null
            }
        },
        // ()
        created(){
        },
        // 
        methods: {
          changeData(data){
            this.tableData = data
          }
        },
        // 
        computed: {},
        // 
        watch: {
          currentNode(data){
              console.log('',data)
          }
        },
        // 
        components: {
          List,
          Buttons,
          Search
        }
    }
</script>

