<template>
  <el-dialog :value="value" name="UserDialog" :visible="isShow" :before-close="handleClose" :title="title" width="750px" center>
    <el-form ref="form" :model="form" :rules="rule" label-width="110px" label-position="right">
      <tr>
        <td><el-form-item label="" prop="nickname">
          <el-input v-model="form.nickname" size="medium" :maxlength="30" placeholder=""/>
        </el-form-item></td>
        <td><el-form-item label="" prop="datelasttime ">
          <el-date-picker
            v-model="form.datelasttime" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="equipment ">
          <el-input v-model="form.equipment " size="medium" :maxlength="30" placeholder=""/>
        </el-form-item></td>
        <td><el-form-item label="" prop="dangerous">
          <el-input v-model="form.dangerous" size="medium" :maxlength="30" placeholder=""/>
        </el-form-item></td>
      </tr>


    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button v-if="type == 0" size="medium" width="long" @click="cancelClick"> </el-button>
      <template v-else>
        <el-button size="medium" width="long" @click="cancelClick"> </el-button>
        <el-button size="medium" width="long" type="primary" @click="sureClick"> </el-button>
      </template>
    </span>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Validator from '@/lib/validator'
import UserService from '@/services/UserService'
import ElFormItem from "../../../../node_modules/element-ui/packages/form/src/form-item";

export default {
  components: {ElFormItem},
  name: 'UserDialog',
  props: {
    userData: { // 
      type: Object,
      default() {
        return {}
      }
    },
    type: { // : 0-(),1-,2-
      type: Number,
      default: 0
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    type(val) {
      let title = '';
      if(!val){
          val = 0
      }
      switch (val) {
        case 1:
          title = '';
          break;
        case 2:
          title = '';
          break;
        default:
          title = '';
          break
      }
      this.title = title
    },
    value(val) {
      this.isShow = val
    },
    isShow(val) {
      this.$emit('input', val)
    },
    userData(data) {
      this.form.id = data.id;
      this.form.account = data.account;
      this.form.nickname = data.nickname;
      this.form.gender = data.gender;
      this.form.type = data.type;
      this.form.subject = data.subject;
      this.form.depart = data.depart;
      this.form.group = data.group;
      this.form.description = data.description;
      this.form.nation = data.nation;
      this.form.nativePlace = data.nativePlace;
      this.form.dateOfBirth = data.dateOfBirth;
      this.form.education = data.education;
      this.form.mobilePhone = data.mobilePhone;
      this.form.telephone = data.telephone;
      this.form.weChat = data.weChat;
      this.form.email = data.email;
      this.form.address = data.address;
      this.from.idCard = data.idCard;
      this.form.number = data.number;
      this.form.administrative = data.administrative;
      this.form.politicsStatus = data.politicsStatus;
      this.form.peopleType = data.peopleType;
      this.form.partyBranch = data.partyBranch;
      this.form.partyPosts = data.partyPosts
      this.form.equipment = data.equipment;
    }
  },
  data() {
    return {
      isShow: false,
      title:'',
      form: {
        imageUrl:'',
        id: '',
        account: '',
        password: '',
        nickname: '',
        gender: -1,
        type: 2,
        subject: '',
        depart: '',
        group: '',
        description: '',
        nation:'',
        nativePlace:'',
        dateOfBirth:'',
        education:'',
        mobilePhone:'',
        telephone:'',
        weChat:'',
        email:'',
        address:'',
        administrative:'',
        politicsStatus:'',
        idCard:'',
        peopleType:'',
        partyBranch:'',
        partyPosts:'',
        equipment:'',
      },
      rule: {
        name: [{
          validator: Validator.checkName,
          trigger: 'blur'
        }]
      },
      options:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
        {label:'',value:4},
        {label:'',value:5},
        {label:'',value:6},
        {label:'',value:7},
      ],
      politicsStatus:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
      ],
      peopleType:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
        {label:'',value:4},
        {label:'',value:5},
      ]
    }
  },
  computed: {
//    ...mapState([
//      'subjectList',
//      'departList',
//      'groupList'
//    ]),
  },
//  async mounted() {
//    await this.getSubjectList()
//    await this.getDepartList()
//    await this.getGroupList()
//  },
  methods: {
    ...mapActions([
      'getSubjectList',
      'getDepartList',
      'getGroupList',
      'getUserList'
    ]),
    selectSubject(val) {
      this.form.subject = val
    },
    selectDepart(val) {
      this.form.depart = val
    },
    selectGroup(val) {
      this.form.group = val
    },
    // 
    sureClick() {
      switch (this.type) {
        case 1: // 
          UserService.addUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        case 2: // 
          // 
          UserService.updateUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        default:
          break
      }
    },

    // 
    cancelClick() {
      this.isShow = false
    },
    // 
    handleClose() {
      this.isShow = false
    },
    // 
    handleAvatarSuccess(res, file) {
      this.form.imageUrl = URL.createObjectURL(file.raw);
    },
  }
}
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px solid #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



