<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <el-dialog :value="value" name="UserDialog" :visible="isShow" :before-close="handleClose" :title="title" width="750px" center>
    <el-form ref="form" :model="form" :rules="rule" label-width="100px" label-position="right">
      <tr>
        <td><el-form-item label="" prop="imgUrl">
            <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess">
              <img v-if="form.imageUrl" :src="form.imageUrl" class="avatar">
              <i v-else class="el-icon-plus uploader-icon"></i>
            </el-upload>
          </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="account">
            <el-input v-model="form.account" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
          </el-form-item></td>
        <td><el-form-item label="" prop="nickname">
          <el-input v-model="form.nickname" size="medium" :maxlength="30" placeholder=""/>
        </el-form-item></td>
      </tr>
      <tr>
        <td v-if="type == 0 || type == 1"><el-form-item v-if="type == 0 || type == 1" label="" prop="password">
            <el-input type="password" v-model="form.password" size="medium" :maxlength="30" auto-complete="new-password" placeholder=""/>
          </el-form-item></td>
        <td><el-form-item label="" prop="gender">
            <el-radio v-model="form.gender" :label="0"></el-radio>
            <el-radio v-model="form.gender" :label="1"></el-radio>
          <el-radio v-model="form.gender" :label="-1"></el-radio>
          </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="nation ">
            <el-input v-model="form.nation" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
          </el-form-item></td>
        <td><el-form-item label="" prop="nativePlace">
          <el-input v-model="form.nativePlace" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="dateOfBirth ">
          <el-date-picker
            v-model="form.dateOfBirth" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item></td>
        <td><el-form-item label="" prop="idCard">
          <el-input v-model="form.idCard" size="medium" :maxlength="30" auto-complete="new-account" placeholder="" type="number"/>
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="peopletype">
          <el-select v-model="form.peopletype" placeholder="" @click="btnClick()">
            <el-option
              v-for="item in optionss"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item></td>
        <td><el-form-item label="" prop="dangtype">
          <el-radio v-model="form.dangtype" :label="0"></el-radio>
          <el-radio v-model="form.dangtype" :label="1"></el-radio>
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="number ">
          <el-input v-model="form.number" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
        </el-form-item></td>
        <td><el-form-item label="" prop="education">
          <el-select v-model="form.education" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="administrative ">
          <el-input v-model="form.administrative" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
        </el-form-item></td>
        <td><el-form-item label="" prop="politicsStatus ">
          <el-select v-model="form.education" placeholder="">
            <el-option
              v-for="item in politicsStatus"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <!--<el-input v-model="form.politicsStatus" size="medium" :maxlength="30" auto-complete="new-account" placeholder="" />-->
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="weChat ">
          <el-input v-model="form.weChat" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
        </el-form-item></td>
        <td><el-form-item label="" prop="email">
          <el-input v-model="form.email" size="medium" :maxlength="30" auto-complete="new-account" placeholder="" />
        </el-form-item></td>
      </tr>
      <tr>
        <td><el-form-item label="" prop="weChat ">
          <el-input v-model="form.mobilePhone" size="medium" :maxlength="11" auto-complete="new-account" placeholder="" type="number"/>
        </el-form-item></td>
        <td><el-form-item label="" prop="email">
          <el-input v-model="form.telephone" size="medium" :maxlength="30" auto-complete="new-account" placeholder=""/>
        </el-form-item></td>
      </tr>
      <tr>
        <td v-if="this.form.peopletype == 1||this.form.peopletype == 2||this.form.peopletype == 3||this.form.peopletype == 4 "><el-form-item label="" prop="dateOfgo">
          <el-date-picker
            v-model="form.dateOfgo" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item></td>
        <td v-if="this.form.peopletype == 1||this.form.peopletype == 2||this.form.peopletype == 3"><el-form-item label="" prop="dataofshor">
          <el-date-picker
            v-model="form.dataofshor" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item></td>
      </tr>
      <tr>
        <td v-if="this.form.peopletype == 1"><el-form-item label="" prop="dangdate">
          <el-date-picker
            v-model="form.dangdate" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item></td>
      </tr>
      <tr>
        <td colspan="2"><el-form-item label="" prop="description">
            <el-input type="textarea" v-model="form.address" :rows="2" :maxlength="100" placeholder="" show-word-limit/>
          </el-form-item></td>
      </tr>
      <tr>
        <td colspan="2"><el-form-item label="" prop="description">
            <el-input type="textarea" v-model="form.description" :rows="5" :maxlength="255" placeholder="" show-word-limit/>
          </el-form-item></td>
      </tr>

      <!--<el-form-item label="" prop="type">-->
        <!--<el-radio v-model="form.type" :label="0"></el-radio>-->
        <!--<el-radio v-model="form.type" :label="1"></el-radio>-->
        <!--<el-radio v-model="form.type" :label="2"></el-radio>-->
      <!--</el-form-item>-->
      <!--<el-form-item label="" prop="subject">-->
        <!--<el-select v-model="form.subject" size="medium" placeholder="" @change="selectSubject">-->
          <!--<el-option v-for="item in subjectList" :key="item.id" :label="item.name" :value="item.id"/>-->
        <!--</el-select>-->
      <!--</el-form-item>-->
      <!--<el-form-item label="()" prop="depart">-->
        <!--<el-select v-model="form.depart" size="medium" placeholder="()" @change="selectDepart">-->
          <!--<el-option v-for="item in departList" :key="item.id" :label="item.name" :value="item.id"/>-->
        <!--</el-select>-->
      <!--</el-form-item>-->
      <!--<el-form-item label="()" prop="group">-->
        <!--<el-select v-model="form.group" size="medium" placeholder="()" @change="selectGroup">-->
          <!--<el-option v-for="item in groupList" :key="item.id" :label="item.name" :value="item.id"/>-->
        <!--</el-select>-->
      <!--</el-form-item>-->

    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button v-if="type == 0" size="medium" width="long" @click="cancelClick"> </el-button>
      <template v-else>
        <el-button size="medium" width="long" @click="cancelClick"> </el-button>
        <el-button size="medium" width="long" type="primary" @click="sureClick"> </el-button>
      </template>
    </span>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Validator from '@/lib/validator'
import UserService from '@/services/UserService'
import ElFormItem from "../../../../node_modules/element-ui/packages/form/src/form-item";

export default {
  components: {ElFormItem},
  name: 'UserDialog',
  props: {
    userData: { // 
      type: Object,
      default() {
        return {}
      }
    },
    type: { // : 0-(),1-,2-
      type: Number,
      default: 0
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  watch: {
      'form.peopletype'(val){
        console.log("form.peopletype",val)
      },
    type(val) {
      let title = '';
      if(!val){
          val = 0
      }
      switch (val) {
        case 1:
          title = '';
          break;
        case 2:
          title = '';
          break;
        default:
          title = '';
          break
      }
      this.title = title
    },
    value(val) {
      this.isShow = val
    },
    isShow(val) {
      this.$emit('input', val)
    },
    userData(data) {
      this.form.id = data.id;
      this.form.account = data.account;
      this.form.nickname = data.nickname;
      this.form.gender = data.gender;
      this.form.type = data.type;
      this.form.subject = data.subject;
      this.form.depart = data.depart;
      this.form.group = data.group;
      this.form.description = data.description;
      this.form.nation = data.nation;
      this.form.nativePlace = data.nativePlace;
      this.form.dateOfBirth = data.dateOfBirth;
      this.form.education = data.education;
      this.form.mobilePhone = data.mobilePhone;
      this.form.telephone = data.telephone;
      this.form.weChat = data.weChat;
      this.form.email = data.email;
      this.form.address = data.address;
      this.from.idCard = data.idCard;
      this.form.number = data.number;
      this.form.administrative = data.administrative;
      this.form.politicsStatus = data.politicsStatus;
      this.form.peopletype = data.peopletype;
      this.form.dangtype = data.dangtype;
      this.form.dateOfgo = data.dateOfgo;
      this.form.dataofshor = data.dataofshor;
      this.form.dangdate = data.dangdate;

    }
  },
  data() {
    return {
      isShow: false,
      title:'',
      form: {
        imageUrl:'',
        id: '',
        account: '',
        password: '',
        nickname: '',
        gender: -1,
        type: 2,
        subject: '',
        depart: '',
        group: '',
        description: '',
        nation:'',
        nativePlace:'',
        dateOfBirth:'',
        education:'',
        mobilePhone:'',
        telephone:'',
        weChat:'',
        email:'',
        address:'',
        administrative:'',
        politicsStatus:'',
        peopletype:'',
        dangtype: '',

        dateOfgo:'',
        dataofshor:'',
        dangdate:'',

      },
      rule: {
        name: [{
          validator: Validator.checkName,
          trigger: 'blur'
        }]
      },
      options:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
        {label:'',value:4},
        {label:'',value:5},
        {label:'',value:6},
        {label:'',value:7},
      ],
      optionss:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
        {label:'',value:4},
        {label:'',value:5},
      ],
      politicsStatus:[
        {label:'',value:1},
        {label:'',value:2},
        {label:'',value:3},
      ]
    }
  },
  computed: {
//    ...mapState([
//      'subjectList',
//      'departList',
//      'groupList'
//    ]),
  },
//  async mounted() {
//    await this.getSubjectList()
//    await this.getDepartList()
//    await this.getGroupList()
//  },
  methods: {
    ...mapActions([
      'getSubjectList',
      'getDepartList',
      'getGroupList',
      'getUserList'
    ]),

    selectSubject(val) {
      this.form.subject = val
    },
    selectDepart(val) {
      this.form.depart = val
    },
    selectGroup(val) {
      this.form.group = val
    },
    // 
    sureClick() {
      switch (this.type) {
        case 1: // 
          UserService.addUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        case 2: // 
          // 
          UserService.updateUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        default:
          break
      }
    },

    // 
    cancelClick() {
      this.isShow = false
    },
    // 
    handleClose() {
      this.isShow = false
    },
    // 
    handleAvatarSuccess(res, file) {
      this.form.imageUrl = URL.createObjectURL(file.raw);
    },
  }
}
</script>

<style scoped>
  .avatar-uploader .el-upload {
    border: 1px solid #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



