<template>
  <div name="List" class="list-panel">
    <!--<buttons></buttons>-->
    <el-table ref="multipleTable" :data="userList"   highlight-current-row @current-change="handleTableChange" height="520">
      <el-table-column prop="accountid" label="" width="200"/>
      <el-table-column prop="nickname" label="" width="210"/>
      <el-table-column prop="datelasttime" label="" width="200"/>
      <el-table-column prop="usedrug" label="" width="210"/>
      <el-table-column prop="drugover" label="" width="210"/>
      <el-table-column prop="dangerous" label="" width="210"/>
    </el-table>
    <el-pagination background layout="prev, pager, next" :total="1000"/>
    <!--<user-dialog v-model="isUserDialogShow" :type="dialogType" :userData="userData"/>-->
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Bus from '../../bus/bus01.js'

export default {
  name: 'List',
  components: {
  },
  data() {
    return {
      isUserDialogShow: false,
      dialogType: 0,
      userData: {},
      changeTableData: null
    }
  },
  computed: {
    ...mapState([
      'userList'
    ])
  },
  mounted() {
    this.getUserList();
    //Tree
    Bus.$on('txt',(data)=> {
      this.setUserList(state,data)
      this.getUserList();
    })
  },
  methods: {
    ...mapActions([
      'getUserList'
    ]),
    // 
    handleTableChange(val) {
      this.changeTableData = val;
      this.$emit('tableDataChange',val)
    },
//    // 
//    viewUser(data) {
//      console.log('data =viewUser= ', data)
//
//      this.dialogType = 0
//      this.isUserDialogShow = true
//      this.userData = data
//    },
//    // 
//    updateUser(data) {
//      console.log('data =updateUser= ', data)
//
//      this.dialogType = 2
//      this.isUserDialogShow = true
//      this.userData = data
//    },
//    // 
//    deleteUser(data) {
//      console.log('data =deleteUser= ', data)
//
//      this.$confirm(', ?', '', {
//        confirmButtonText: '',
//        cancelButtonText: '',
//        type: 'warning'
//      }).then(() => {
//        this.$message.success('')
//      }).catch(() => {
//        this.$message.info('')
//      })
//    }
  }
}
</script>

<style scoped>
.el-pagination {
  margin: 10px 0;
  float: right;
}
</style>



