#@Author: bjy @Date: 2019/6/11 10:12 #
<template>
    <div name="buttons" class="buttons">
      <el-row>
        <el-col :span="12">
          <el-button type="info" icon="el-icon-document" @click="look" ></el-button>
          <el-button type="success" icon="el-icon-delete" @click="ImportExcel" ></el-button>
        </el-col>
        <el-col :span="12" style="text-align: right">
          <el-button type="primary" icon="el-icon-s-data" @click="rankingOfDay" ></el-button>
          <el-button type="primary" icon="el-icon-s-data" @click="rankingOfWeek"></el-button>
          <el-button type="primary" icon="el-icon-s-data" @click="rankingOfMonth"></el-button>
          <el-button type="primary" icon="el-icon-s-data" @click="rankingOfYear"></el-button>
        </el-col>
      </el-row>
      <import-dialog v-model="isImportDialogShow"></import-dialog>
      <userDialog v-model="isUserDialogShow" :type="dialogType" :userData="userData"></userDialog>
    </div>
</template>

<script>
  import userDialog from '../UserDialog/UserDialog.vue'
  import importDialog from '../ImportDialog/ImportDialog.vue'
  import userService from '../../../services/UserService.js'
  import ElCol from "element-ui/packages/col/src/col";
  import ElRow from "element-ui/packages/row/src/row";

    export default{
        name: 'buttons',
        props:['changeData'],
        data () {
            return {
              isUserDialogShow:false,
              isImportDialogShow:false,
              dialogType:0,// : 0-(),1-,2-
              tableData:'',
              userData:{}
            }
        },
        // ()
        created(){
        },
        // 
        methods: {
            // 
          look(){
            if(!this.tableData){
              this.$message.error(',');
              return
            }
            console.log('');
            this.dialogType = 0;
            this.isUserDialogShow = true;
            this.userData = this.tableData
          },
          // 
          ImportExcel(){
              console.log('')
              this.isImportDialogShow = true
          },
          // 
          rankingOfDay(){
            console.log('');
          },
          // 
          rankingOfWeek(){
              console.log('')
          },
          // 
          rankingOfMonth(){
            console.log('');
          },
          // 
          rankingOfYear(){
              console.log('')
          },
        },
        // 
        watch: {
          changeData(tableData) {
              this.tableData = tableData
          }
        },
        // 
      components: {
          ElRow,
          ElCol,
          userDialog,
        importDialog
        }
    }
</script>

<!--  -->
<style scoped>
  .el-row + .el-row {
    margin: 5px 0;
  }
  .button-search{
    display: flex;
    justify-content: space-between;
    align-content: center;
  }
</style>



