#@Author: bjy @Date: 2019/6/12 19:18 #
<template>
  <el-dialog :value="value" name="ImportDialog" :visible="isShow" :before-close="handleClose" title="" width="800px" left>
    <el-form  ref="form" label-width="100px" label-position="right" class="i-form">
      <el-form-item label="">
        <a href="http://www.ydyy.online/partyBuilding/static/peopleImport.xls" rel="external nofollow" download="" >
          <el-button type="warning" class="i-button" icon="el-icon-download"></el-button>
        </a>
        <span class="remark"> : :</span>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="upload-demo"
          ref="upload"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          :auto-upload="false">
          <el-button type="warning"  slot="trigger"  style="width: 160px" class="i-button" icon="el-icon-folder-opened"></el-button>
        </el-upload>
      </el-form-item>
      <el-form-item label="">
        <el-button type="warning"  class="i-button" @click="submitUpload" icon="el-icon-upload2"></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>


<script>
  import ElFormItem from "../../../../node_modules/element-ui/packages/form/src/form-item";
  import ElForm from "../../../../node_modules/element-ui/packages/form/src/form";
  import ElButton from "../../../../node_modules/element-ui/packages/button/src/button";
    //  ,,:import xxx from ""
    export default{
        // name,
        name: 'importDialog',
        props:{
          value:{
            type:Boolean,
            default: false
          },
          type: { // : 0-(),1-,2-
            type: Number,
            default: 0
          },
        },
        //  
        data () {
            return {
              isShow:false,
              fileList: []
            }
        },
        // ()
        created(){
        },
        // 
        methods: {
            // 
          submitUpload() {
            this.$refs.upload.submit();
          },
          handleRemove(file, fileList) {
            console.log(file, fileList);
          },
          handlePreview(file) {
            console.log(file);
          },
          // 
          handleClose() {
            this.isShow = false
          },
        },
        // 
        computed: {},
        // 
        watch: {
          value(val) {
            this.isShow = val
          },
          isShow(val) {
            this.$emit('input', val)
          },
        },
      /**
       *
       */
        // 
        components: {
          ElButton,
          ElForm,
          ElFormItem}
    }
</script>

<!--  -->
<style scoped>
  .remark{
    margin-left: 10px;
    font-size: 15px;
  }
  .i-button{
    width: 160px;
  }
  .upload-demo{
    width: 55%
  }
  .i-form{
    padding-bottom: 50px;
  }
</style>



