<template>
  <el-main name="MenuManageDemo">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index/MenuManageDemo">{{ pageTitle }}</el-breadcrumb-item>
    </el-breadcrumb>
    <!--<div class="content-panel">-->
      <!--<search/>-->
    <!--</div>-->
    <div class="content-panel">
      <el-row>
        <el-col :span="4">
          <tree @nodeDept="thisDept"/>
        </el-col>
        <el-col :span="20">
          <manage :currentNode="currentNode"/>
        </el-col>
      </el-row>
    </div>
  </el-main>
</template>

<script>
//  import Search from '@/components/MenuManage/Search/Search.vue'
  import Tree from '@/components/MenuManage/Tree/Tree.vue'
  import Manage from '@/components/MenuManage/Manage/Manage.vue'
    //  ,,:import xxx from ""
    export default{
        // name,
        name: 'menuManage',
        //  
        data () {
            return {
              pageTitle:'',
              currentNode:null
            }
        },
        // ()
        created(){
        },
        // 
        methods: {
          thisDept(data){
            this.currentNode = data
          }
        },
        // 
        computed: {},
        // 
        watch: {},
        // 
      components: {
        Tree,Manage
        }
    }
</script>

<!--  -->
<style scoped>
  .content-panel {
    margin-top: 20px;
  }
</style>



