#@Author: bjy @Date: 2019/6/11 10:12 #
<template>
    <div  class="buttons">
      <!--<div class="button-search">-->
        <div>
          <el-button type="info" icon="el-icon-document" @click="look" ></el-button>
          <el-button type="primary" icon="el-icon-plus" @click="addUser"></el-button>
          <el-button type="success" icon="el-icon-edit" @click="updateUser"></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="delUser"></el-button>
        </div>
        <!--<div>-->
      <!--<search/>-->
      <!--</div>-->
      <userDialog v-model="isUserDialogShow" :type="dialogType" :userData="userData"></userDialog>
    </div>
</template>

<script>
  import userDialog from '../UserDialog/UserDialog.vue'
  import userService from '../../../services/UserService.js'
  import ElCol from "element-ui/packages/col/src/col";
  import Search from '../Search/Search.vue'

    export default{
        name: 'buttons',
        props:['changeData'],
        data () {
            return {
              isUserDialogShow:false,
              dialogType:0,// : 0-(),1-,2-
              tableData:'',
              userData:{}
            }
        },
        // ()
        created(){
        },
        // 
        methods: {
            // 
          look(){
            if(!this.tableData){
              this.$message.error(',');
              return
            }
            console.log('');
            this.dialogType = 0;
            this.isUserDialogShow = true;
            this.userData = this.tableData
          },
          // 
          addUser(){
            console.log('');
            this.dialogType = 1;
            this.isUserDialogShow = true;
            this.userData = {}
          },
          // 
          updateUser(){
            if(!this.tableData){
              this.$message.error(',');
              return
            }
            this.dialogType = 2;
            this.isUserDialogShow = true;
            console.log('');
            this.userData = this.tableData
          },
          // 
          delUser(){
            if(!this.tableData){
              this.$message.error(',')
              return
            }
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.$message.success('')
            }).catch(() => {
              this.$message.info('')
            })
          },
        },
        // 
        watch: {
          changeData(tableData) {
              this.tableData = tableData
          }
        },
        // 
        components: {
          ElCol,
          userDialog,
          Search
        }
    }
</script>

<!--  -->
<style scoped>
  .el-row + .el-row {
    margin: 5px 0;
  }
  .button-search{
    display: flex;
    justify-content: space-between;
    align-content: center;
  }
</style>



