<template>
  <el-main name="Activity">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index/ActivityManage">{{ pageTitle }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="content-panel">
      <search/>
    </div>
    <div class="content-panel">
      <list/>
    </div>
  </el-main>
</template>

<script>
  import Search from '../components/ActivityManage/Search/Search.vue'
  import List from '../components/ActivityManage/List/List.vue'
    //  ,,:import xxx from ""
    export default{
        // name,
        name: 'activity',
        //  
        data () {
            return {
              pageTitle:''
            }
        },
        // ()
        created(){
        },
        // 
        methods: {},
        // 
        computed: {},
        // 
        watch: {},
        // 
        components: {
          Search,List
        }
    }
</script>

<!--  -->
<style scoped>
  .content-panel {
    margin-top: 20px;
  }
</style>



