<template>
  <el-dialog
    :value="value"
    name="UserDialog"
    :visible="isShow"
    :before-close="handleClose"
    :title="title"
    width="640px"
    center
  >
    <el-form ref="form" :model="form" :rules="rule" label-width="100px">
      <el-form-item label="" prop="account">
        <el-input
          v-model="form.account"
          size="medium"
          :maxlength="30"
          auto-complete="new-account"
          placeholder=""
        />
      </el-form-item>
      <el-form-item v-if="type == 0" label="" prop="password">
        <el-input
          type="password"
          v-model="form.password"
          size="medium"
          :maxlength="30"
          auto-complete="new-password"
          placeholder=""
        />
      </el-form-item>
      <el-form-item label="" prop="nickname">
        <el-input v-model="form.nickname" size="medium" :maxlength="30" placeholder=""/>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-radio v-model="form.gender" :label="0"></el-radio>
        <el-radio v-model="form.gender" :label="1"></el-radio>
        <el-radio v-model="form.gender" :label="-1"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-radio v-model="form.type" :label="0"></el-radio>
        <el-radio v-model="form.type" :label="1"></el-radio>
        <el-radio v-model="form.type" :label="2"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="subject">
        <el-select v-model="form.subject" size="medium" placeholder="" @change="selectSubject">
          <el-option
            v-for="item in subjectList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="()" prop="depart">
        <el-select
          v-model="form.depart"
          size="medium"
          placeholder="()"
          @change="selectDepart"
        >
          <el-option v-for="item in departList" :key="item.id" :label="item.name" :value="item.id"/>
        </el-select>
      </el-form-item>
      <el-form-item label="()" prop="group">
        <el-select v-model="form.group" size="medium" placeholder="()" @change="selectGroup">
          <el-option v-for="item in groupList" :key="item.id" :label="item.name" :value="item.id"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="description">
        <el-input
          type="textarea"
          v-model="form.description"
          :rows="5"
          :maxlength="255"
          placeholder=""
        />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button v-if="type == 0" size="medium" width="long" @click="cancelClick"> </el-button>
      <template v-else>
        <el-button size="medium" width="long" @click="cancelClick"> </el-button>
        <el-button size="medium" width="long" type="primary" @click="sureClick"> </el-button>
      </template>
    </span>
  </el-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Validator from '@/lib/validator'
import UserService from '@/services/UserService'

export default {
  name: 'UserDialog',
  props: {
    userData: { // 
      type: Object,
      default() {
        return {}
      }
    },
    type: { // : 0-(),1-,2-
      type: Number,
      default: 0
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    type(val) {
      let title = '';
      console.log('type--',this.type);
      if(!val){
          val = 0
      }
      switch (val) {
        case 1:
          title = '';
//          this.$refs.form.resetFields();
          break;
        case 2:
          title = '';
          break;
        default:
          title = '';
          break
      }
      this.title = title
    },
    value(val) {
      this.isShow = val
    },
    isShow(val) {
      this.$emit('input', val)
    },
    userData(data) {
      this.form.id = data.id;
      this.form.account = data.account;
      this.form.nickname = data.nickname;
      this.form.gender = data.gender;
      this.form.type = data.type;
      this.form.subject = data.subject;
      this.form.depart = data.depart;
      this.form.group = data.group;
      this.form.description = data.description
    }
  },
  data() {
    return {
      isShow: false,
      title:'',
      form: {
        id: '',
        account: '',
        password: '',
        nickname: '',
        gender: -1,
        type: 2,
        subject: '',
        depart: '',
        group: '',
        description: ''
      },
      rule: {
        name: [{
          validator: Validator.checkName,
          trigger: 'blur'
        }]
      }
    }
  },
  computed: {
    ...mapState([
      'subjectList',
      'departList',
      'groupList'
    ]),
  },
  async mounted() {
    await this.getSubjectList()
    await this.getDepartList()
    await this.getGroupList()
  },
  methods: {
    ...mapActions([
      'getSubjectList',
      'getDepartList',
      'getGroupList',
      'getUserList'
    ]),
    selectSubject(val) {
      this.form.subject = val
    },
    selectDepart(val) {
      this.form.depart = val
    },
    selectGroup(val) {
      this.form.group = val
    },
    // 
    sureClick() {
      switch (this.type) {
        case 1: // 
          UserService.addUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        case 2: // 
          // 
          UserService.updateUser(this.form).then((res) => {
            this.$message.success('');

            this.getUserList();
            this.isShow = false
          });
          break;
        default:
          break
      }
    },
    // 
    cancelClick() {
      this.isShow = false
    },
    // 
    handleClose() {
      this.isShow = false
    }
  }
}
</script>



