#@Author: bjy @Date: 2019/5/17 15:40 #
<template>
  <el-main name="UEditorDemo">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index/UEditorDemo">{{ pageTitle }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="content-panel">
      <UEditor ref="Ueditor" id="ueditor" :config="config"/>
    </div>
  </el-main>
</template>

<script>
import UEditor from '@/components/UEditor/UEditor.vue'

export default {
  name: 'UEditorDemo',
  components: {
    UEditor
  },
  data() {
    return {
      pageTitle: 'UEditorDemo',
      config: {
        autoHeightEnabled: false,
        autoFloatEnabled: true,
        initialContent: '', // ,textarea/script
        autoClearinitialContent: true, // focustrue,
        initialFrameWidth: null,
        initialFrameHeight: 600,
        BaseUrl: '',
        UEDITOR_HOME_URL: 'static/ueditor/'
      }
    }
  },
  // ()
  created() {
  },
  // 
  methods: {},
  // 
  computed: {},
  // 
  watch: {}
}
</script>

<style scoped>
.content-panel {
  margin-top: 20px;
}
</style>



