<template>
  <el-main name="DemocraticParties">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index"></el-breadcrumb-item>
      <el-breadcrumb-item to="/Index/RetiredVeteranCadre">{{ pageTitle }}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="content-panel">
      <el-row>
        <el-col :span="4">
          <tree @nodeDept="thisDept"/>
        </el-col>
        <el-col :span="20">
          <manage :currentNode="currentNode"/>
        </el-col>
      </el-row>
    </div>
  </el-main>
</template>

<script>
  import Manage from '@/components/RetiredVeteranCadre/Manage/Manage.vue'
  import Tree from '@/components/RetiredVeteranCadre/Tree/Tree.vue'

  export default{
    // name,
    name: 'democraticParties',
    //  
    data () {
      return {
        pageTitle:'',
        currentNode:null
      }
    },
    // ()
    created(){
    },
    // 
    methods: {
      thisDept(data) {
        this.currentNode = data
      },
    },
    // 
    computed: {},
    // 
    watch: {},
    // 
    components: {
      Manage,
      Tree
    }
  }
</script>

<!--  -->
<style scoped>
  .content-panel {
    margin-top: 20px;
  }
</style>



